export function post(method: string, params: string[], timeout = 5) {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      reject(`timeout: ${timeout}`);
    }, timeout);
    fetch("/api", {
      method: "POST",
      body: JSON.stringify({
        method,
        params,
      }),
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then(resp => {
        return resp.json();
      })
      .then(data => {
        if (data.success) {
          resolve(data.result);
        } else {
          reject(data.error || data);
        }
      })
      .catch(reject);
  });
}
