import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Back from "../back/Back";
import "./Chargers.css";

function Chargers() {
  const [chargers, setChargers] = useState([] as number[]);
  useEffect(() => {
    setChargers([1, 2, 3, 4, 5]);
  }, []);
  return (
    <>
      <Back title="查看充电桩列表" />
      {chargers.map(v => (
        <Link to={v.toString()} key={v}>
          {v}
        </Link>
      ))}
    </>
  );
}

export default Chargers;
